.MainMenuWrapper{
    max-width: 480px;
    margin: 0 auto;
}

.MainMenuWrapper h4{
    display: inline-block;
    padding: 0 20px 5px;
    border-bottom: 1px solid;
    font-size: 1.125rem;
    letter-spacing: 2px;
    margin-top: 20px;
}

.MainMenu{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    grid-column-gap: 10px;
    grid-row-gap: 15px;
    margin:0 20px;
}

.MainMenu .Item{
    height: 90px;
    border: 1px solid #313638;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 2px 4px #403d39;
}

.MainMenuWrapper .MainMenu a:active{
    background-color: #e0dfd5;
}

.Info{
    width: 80%;
    margin: 60px auto 0;
}

.Info p{
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-height:340px) {
    .MainMenuWrapper h4{
        margin-bottom: 10px;
    }
    .MainMenu .Item{
        height: 80px;
    }
    .MainMenu .Item h3{
        font-size: 1.15rem;
    }
}