.BottomMenuWrapper{
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 0.5rem 0;
    left:0;
    background-color: #313638;
    
}

.BottomMenu{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    max-width: 320px;
    margin:0 auto
}

.BottomMenu a{
    padding: 10px 5px;
    /* border: 1px solid; */
    width: 90px;
    color: #e8e9eb;
}

.BottomMenu a:active{
    background-color: #e0dfd5;
}

.Central{
    border-left: 1px solid; 
    border-right: 1px solid; 
}