.PriceListWrapper{
    max-width: 480px;
    margin: 0 auto;
    /*overflow-y: auto;
    max-height: 720px;*/
    padding-bottom: 60px;

}

.PriceListHeader{
    display: inline-block;
    padding: 0 20px 5px;
    border-bottom: 1px solid;
    font-size: 1.35rem;
    letter-spacing: 5px;
    margin-top: 20px;
}
.PriceListItem{
    width: 90%;
    margin: 0 auto;
    text-align: left;
    font-size: 1.2rem;
    padding: 5px 10px;
    background-color: #e8e9eb;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ElementName{
    font-weight: 300;
}

.ElementPrice{
    text-align: right;
    color:green;
}


.PriceListItem:nth-of-type(even){
    background-color: #e0dfd5;
}

